.container {
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
    background: #40407a;
    color: #f7f1e3;
    flex-direction: column;
}

.App {
    width: 500px;
    height: 330px;
    border: 5px inset #f7f1e3;
    border-radius: 20%;
}

.title {
    text-align: center;
}

.time-control {
    display: flex;
    justify-content: space-evenly;
}

#break-label, #session-label {
    margin-bottom: 8px;
}

.break-control, .session-control {
    display: flex;
    justify-content: space-evenly;
}

#timer-label {
    text-align: center;
}

#time-left {
    font-size: 5rem;
    line-height: 4.5rem;
    margin-bottom: 15px;
    text-align: center;
}

.start-stop-wrapper {
    display: flex;
    justify-content: space-evenly;
}

button {
    background: #40407a;
    color: #f7f1e3;
    border-radius: 2px;
}